.import-pixel{
  display: flex;
  flex-wrap: wrap;
  margin-top: 5em;
  &__item{
    position: relative;
    margin: 10px;
    height: 100px;
    border:4px solid #000;
    cursor: pointer;
    canvas{
      height: 100%;
    }
  }
  &__del{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: -4px;
    top: -4px;
    height: 24px;
    width: 24px;
    background-color: #000;
    color: #fff;
  }
}